import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
i18n
    // detect user language
    // learn more: https://github.com/i18next/i18next-browser-languageDetector
    .use(LanguageDetector)
    // pass the i18n instance to react-i18next.
    .use(initReactI18next)
    // init i18next
    // for all options read: https://www.i18next.com/overview/configuration-options
    .init({
        debug: true,
        fallbackLng: 'en',
        interpolation: {
            escapeValue: false, // not needed for react as it escapes by default
        },
        resources: {
            ru: {
                translation: {
                    header: {
                        addInput: ' ...',
                        addButton: '',
                        filters: {
                            all: ' ',
                            done: '',
                            active: '',
                        },
                        searchInput: '  ...',
                        tasksCounter: {
                            left: ' :',
                            empty: '    :(',
                        },
                    },
                    todo: {
                        changeNameInput: '...',
                        buttons: {
                            change: '',
                            delete: '',
                            save: '',
                            cancel: '',
                        },
                    }
                },
            },
            en: {
                translation: {
                    header: {
                        addInput: 'New task...',
                        addButton: 'Add',
                        filters: {
                            all: 'All Tasks',
                            done: 'Done',
                            active: 'Active',
                        },
                        searchInput: 'Task name search...',
                        tasksCounter: {
                            left: 'Tasks left:',
                            empty: 'No tasks :(',
                        }
                    },
					todo: {
                        changeNameInput: 'Task name...',
                        buttons: {
                            change: 'Change',
                            delete: 'Delete',
                            save: 'Save',
                            cancel: 'Cancel',
                        },
                    }
                },
            },
        },
    });

export default i18n;
